.dragArea {
  background-color: #fff;
  min-height: 200px;
  margin-top: 20px;
  padding: 20px;
  box-shadow: 0 2px 4px 0 rgba(42, 49, 60, 0.1);
}
.dragArea .trans-group {
  width: 100%;
  height: 100%;
  display: block;
  min-height: 160px;
}
.dragArea .component-cell {
  display: inline-block;
  border: solid 2px transparent;
  background-color: #fff;
  padding: 10px 15px;
  margin: 0 20px 20px 0;
  line-height: 20px;
  box-shadow: 0 2px 4px 0 rgba(140, 156, 184, 0.3);
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: move;
}
.dragArea .component-cell .del-icon {
  color: #e77272;
  cursor: pointer;
  font-weight: bold;
}
.dragArea .component-cell .del-icon:hover {
  color: #d93030;
}
.dragArea .sortable-chosen {
  border: dotted 2px #1f85c0;
  color: #1f85c0;
  font-weight: bold;
}
.flip-list-move {
  transition: transform 0.5s;
}
.no-move {
  transition: transform 0s;
}
.img-area {
  width: 100%;
  min-height: 10px;
  position: absolute;
  top: 100px;
  left: 14px;
}
.img-area img {
  width: 560px;
  border: solid 5px #ddd;
}
