.dynamic-box {
  background: #fff;
  padding: 20px;
  position: relative;
}
.dynamic-box .ivu-input-wrapper {
  width: 150px;
  margin-right: 8px;
}
.dynamic-box .ivu-select {
  width: 150px;
  margin-right: 8px;
}
.dynamic-box .ivu-checkbox-group {
  display: inline-block;
}
.dynamic-module {
  position: relative;
  padding: 20px;
  border: 1px solid #ddd;
  margin-bottom: 10px;
  background: #fff;
}
.dynamic-tool {
  position: absolute;
  top: 20px;
  right: 20px;
}
.dynamic-module-item {
  margin-bottom: 8px;
}
.dynamic-modal-footer {
  padding: 30px 0;
  text-align: center;
}
.dynamic-search {
  padding: 10px;
  border: 1px solid #ddd;
  background: #eee;
}
